                        {{ slide.title }}
                      </h2>
                      <p class="pr-15">{{ slide.detail }}</p>
                      <!-- <v-card-actions>
                        <v-btn
                          rounded
                          dark
                          class="gradient-button text-capitalize"
                          style="padding: 10px 20px !important"
                          :class="
                            $vuetify.breakpoint.smAndDown ? 'mt-0' : 'mt-5'
                          "
                        >
                          More Info
                        </v-btn>
                      </v-card-actions> -->
                    </div>
                  </div>
                </v-sheet>
              </v-carousel-item>
            </v-carousel>
          </v-col>

          <v-col
