                <v-list-item-avatar rounded="0" size="20" class="mt-7 ml-5">
                  <v-img
                    :src="require('../../views/flights/images/search.svg')"
                  />
                </v-list-item-avatar>
                <v-list-item-content>
                  <div>
                    <v-text-field
                      :class="{
                        widthResponsive: $vuetify.breakpoint.smAndDown,
                      }"
