                      hide-details
                      v-on:keydown="filterName"
                    ></v-text-field>
                  </div>
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-divider class="mt-5 mb-5" vertical></v-divider>
            <v-col cols="12" md="3">
              <v-list-item two-line>
                <v-list-item-avatar rounded="0" size="20" class="mt-7 ml-5">
                  <v-img
                    :src="
                      require('../../views/attheairport/images/fooddrinkicon.svg')
                    "
                  />
                </v-list-item-avatar>
                <v-list-item-content>
                  <div>
                    <v-select
                      :items="allcuisines"
                      hide-details
                      label="All Cuisines"
                      flat
                      v-model="selectCuisines"
                      @change="filterByCuisines"
                    ></v-select>
                  </div>
                  <!-- <v-list-item-subtitle>or Departures</v-list-item-subtitle> -->
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-divider class="mt-5 mb-5" vertical></v-divider>
            <v-col cols="12" md="3">
              <v-list-item two-line>
                <v-list-item-avatar rounded="0" size="20" class="mt-7 ml-5">
                  <v-img
                    :src="
                      require('../../views/flights/images/allterminalIcon.svg')
                    "
                  />
                </v-list-item-avatar>
                <v-list-item-content>
                  <div>
                    <v-select
                      :items="allterminals"
                      hide-details
                      label="All Terminals"
                      flat
                      v-model="selectedTerminal"
                      @change="filterByTerminal"
                    ></v-select>
                  </div>
                  <!-- <v-list-item-subtitle>or Departures</v-list-item-subtitle> -->
                </v-list-item-content>
              </v-list-item>
            </v-col>

            <v-col cols="12" md="2">
              <v-list-item two-line>
                <v-list-item-content>
                  <v-btn rounded large color="#E8EFFE" @click="clearFilter">
                    Reset Filters
                  </v-btn>
                </v-list-item-content>
              </v-list-item>
            </v-col>
          </v-row>
        </v-card>
        <v-container class="bg-surface-variant mt-10 diningdirectoryBlock">
          <v-row
            dense
            v-scrollanimation
            align="center"
            :class="{ charmSmall: $vuetify.breakpoint.smAndDown }"
          >
            <v-col
              class="px-3"
              cols="12"
              sm="6"
              md="4"
              v-for="(resturant, i) in resturants"
              :key="i"
              :class="$vuetify.breakpoint.smAndDown ? 'mb-7' : 'mb-15'"
            >
              <v-card class="mx-auto" height="auto" width="100%">
                <div class="headingBox-txt">
                  <h2>{{ resturant.name }}</h2>
                  <p>{{ resturant.cuisines }}</p>
                </div>

                <v-hover>
                  <v-card
                    :class="`${hover ? 'imgover' : 'imgleave'}`"
                    slot-scope="{ hover }"
                  >
                    <v-img :src="resturant.src" class="mt-5 mb-3"   :class="[$vuetify.breakpoint.smAndDown ? 'img-mob' : 'img-desk']"></v-img>

                  </v-card>
                </v-hover>

                <v-card-actions class="imageBox-txt">
                  <v-card-title v-text="resturant.location"></v-card-title>
                  <v-spacer></v-spacer>
                  <!-- Dialog box Start -->
                  <v-dialog
                    transition="dialog-bottom-transition"
                    max-width="900"
                    style="
                      overflow: hidden !important;
                      box-shadow: none !important;
                    "
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        rounded
                        dark
                        class="gradient-button text-capitalize"
                        style="padding: 10px 20px !important"
                        v-bind="attrs"
                        v-on="on"
                      >
                        More Info
                      </v-btn>
                    </template>
                    <template style="overflow: hidden !important">
                      <v-row
                        style="
                          background: #fff;
                          max-height: 600px;
                          margin: 0 !important;
                        "
                        class="pb-15 rounded-xl"
                        v-for="(dining, i) in dinings"
                        :key="i"
                        :class="$vuetify.breakpoint.smAndDown ? 'pr-0' : 'pr-8'"
                      >
                        <v-col cols="6" style="padding: 0 !important">
                          <v-sheet>
                            <v-hover>
                              <v-card
                                :class="`${hover ? 'imgover' : 'imgleave'}`"
                                slot-scope="{ hover }"
                              >
                                <v-img
                                  :src="dining.src"
                                  class="mt-15"
                                  style="float: left"
                                  :width="
                                    $vuetify.breakpoint.smAndDown
                                      ? '100%'
                                      : '80%'
                                  "
                                ></v-img>
                              </v-card>
                            </v-hover>
                          </v-sheet>
                        </v-col>
                        <v-col cols="6">
                          <v-row>
                            <v-col
                              cols="12"
                              :class="{
                                modalText: $vuetify.breakpoint.smAndDown,
                              }"
                            >
                              <h2 class="text-uppercase page-title mt-10">
                                {{ dining.heading }}
                              </h2>
                              <h1 class="mt-2">{{ dining.name }}</h1>
                              <h3 class="mt-2">
                                <span
                                  class="mdi mdi-silverware-variant mr-2"
                                ></span
                                >{{ dining.cuisines }}
                              </h3>
                              <p class="mt-5">
                                {{ dining.details }}
                              </p>
                            </v-col>
                          </v-row>
                          <v-row
                            :class="
                              $vuetify.breakpoint.smAndDown ? 'mt-0' : 'mt-2'
                            "
                          >
                            <v-col
                              cols="6"
                              :class="{
                                modalText2: $vuetify.breakpoint.smAndDown,
                              }"
                            >
                              <P>Location</P>
                              <span class="dtlrestu">{{
                                dining.location
                              }}</span>
                            </v-col>
                            <v-col
                              cols="6"
                              :class="{
                                modalText2: $vuetify.breakpoint.smAndDown,
                              }"
                            >
                              <P>Timings</P>
                              <span class="dtlrestu">{{ dining.timing }}</span>
                            </v-col>
                          </v-row>
                          <v-row>
                            <v-col
                              cols="12"
                              :class="{
                                charmSmall: $vuetify.breakpoint.smAndDown,
                              }"
                            >
                              <v-btn
                                rounded
                                dark
                                class="gradient-button text-capitalize mr-5"
                                style="padding: 10px 20px 10px 10px !important"
                                :class="
                                  $vuetify.breakpoint.smAndDown
                                    ? 'mt-2'
                                    : 'mt-0'
                                "
                              >
                                <span class="mdi mdi-phone mr-2"></span> Call
                              </v-btn>
                              <v-btn
                                rounded
                                dark
                                class="gradient-button text-capitalize mr-5"
                                style="padding: 10px 20px 10px 10px !important"
                                :class="
                                  $vuetify.breakpoint.smAndDown
                                    ? 'mt-2'
                                    : 'mt-0'
                                "
                              >
                                <span class="mdi mdi-email mr-2"></span> Email
                              </v-btn>
                              <v-btn
                                rounded
                                dark
                                class="gradient-button text-capitalize mr-5"
                                style="padding: 10px 20px 10px 10px !important"
                                :class="
                                  $vuetify.breakpoint.smAndDown
                                    ? 'mt-2'
                                    : 'mt-0'
                                "
                              >
                                <span class="mdi mdi-link-variant mr-2"></span>
                                Website
                              </v-btn>
                            </v-col>
                          </v-row>
                        </v-col>
                      </v-row>
                    </template>
                  </v-dialog>
                  <!-- Dialog box End -->
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>

        <!-- Dining Directory End -->
      </v-container>
    </v-card>
  </div>
</template>
<script>
import axios from "axios";
import rightStickIcon from "../../components/rightStickIcon"

export default {
  components: {
    rightStickIcon,
  },

  data() {
    return {
      search: "",
      allcuisines: [{ text: "All" }], //, { text: "Indigo" }, { text: "Emirates" }

      allterminals: [{ text: "All" }], //, { text: "1" }, { text: "2" }
     
       items: [
          {
            src: require("../../views/attheairport/images/foodDrink-2.jpg"),
          },
          {
            src: require("../../views/attheairport/images/foodDrink-banner.jpg"),
          },

      ],

      slides: [
        {
          title: "Discover the Best Dining Experience at Chennai Airport",
          detail:
            "When it comes to airport dining, Chennai Airport has raised the bar with its impressive selection of restaurants and F&B concepts. Whether opting for a quick bite or a fine dining experience, theres something for everyone at Chennai Airport.",
        },
        // { title: 'Enjoy Gourmet Burgers and Fries at Grillerama', detail: 'Try their famous Gourmet burgers which are made to order and served between brioche buns, along with your favourite side of curly fries.', href: '', },
        // { title: 'Elevate your dining experiences at Street Foods by Punjab Grill', detail: 'Try their famous Gourmet burgers which are made to order and served between brioche buns, along with your favourite side of curly fries.', },
      ],
      resturants: [
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
        // {
        //   name: "Street Foods by Punjab Grill",
        //   cuisines: "North Indian, Mughlai",
        //   src: require("../../views/attheairport/images/dining/small-img/d1.jpg"),
        //   location: "T1 Departures",
        // },
      ],
      resturantsData: [],
      dinings: [
        {
          heading: "Dining",
          name: "Street Foods by Punjab Grill",
          cuisines: "North Indian, Mughlai",
          src: require("../../views/attheairport/images/dining/big-img/fd1.jpg"),
          details:
            "The new breakfast buffet is worth trying especially the Anda Bhurjee and Safed Matter Masala. Bite into the delectable Ambarsari Machhi De Pakode along with your Shorba",
          location: "T1 Departures",
          timing: "Open 24 hours",
        },
      ],
      selectedTerminal: "",
      selectCuisines: "",
      cuisines: [],
      terminalData: [],
    };
  },
  mounted() {
    this.getPoiInfo();
  },
  methods: {
    getPoiInfo() {
      axios
        .post(process.env.VUE_APP_POI_URL + "poisroute/getpois", {
          appname: "maa",
          token:
            "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VyX2lkIjoiNTk1Zjc1NmY1NGVkYWYyMzA4MjYzMTlhIiwiaWF0IjoxNDk5ODc2OTM4LCJleHAiOjE1NTE3MTY5Mzh9.9vFu_xjGMNb_w_1i-qIuxI9pUkdAMFkRqvmHmJAByHk",
        })
        .then((poiData) => {
          
          if (poiData.data.success) {
            let data = poiData.data.data.filter(
              (element) => element.category == "Food & Drink"
            );

            data.forEach((element) => {
              if (this.allcuisines.indexOf(element.subcategory) == -1)
                this.allcuisines.push({ text: element.subcategory });

              if (this.allterminals.indexOf(element.terminal) == -1)
                this.allterminals.push({ text: element.terminal });
              this.allterminals.sort(
                (a, b) => parseFloat(a.text) - parseFloat(b.text)
              );

              this.resturants.push({
                name: element.poi_multilingual["en"].name,
                cuisines: element.subcategory,
                src:
                  element.poi_url != ""
                    ? element.poi_url
                    : require("../../views/attheairport/images/dining/small-img/d1.jpg"),
                location: "T " + element.terminal + " " + element.sliceid,
                terminal: element.terminal,
              });
            });
            this.resturantsData = this.resturants;
            this.cuisines = this.allcuisines;
            this.terminalData = this.allterminals;

            this.selectedTerminal = this.allterminals[1].text;
            console.log("this.selectedTerminal ", this.selectedTerminal);
            this.filterByTerminal(this.selectedTerminal);
          }
        });
    },
    filterName() {
      if (this.search.length) {
        this.allcuisines = [];
        this.allterminals = [];
        this.resturants = this.resturants.filter((f) => {
          if (f.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1) {
            if (this.allcuisines.indexOf(f.cuisines) == -1)
              this.allcuisines.push({ text: f.cuisines });
            if (this.allterminals.indexOf(f.terminal) == -1)
              this.allterminals.push({ text: f.terminal });
            return f;
          }
        });
      } else {
      //  this.selectedTerminal = this.allterminals[1].text;
        this.filterByTerminal(this.selectedTerminal);
      }
    },
    filterByTerminal(terminal) {
      console.log("filterByTerminal", terminal);
      this.allcuisines = [];
      this.resturants = this.resturantsData.filter((f) => {
        if (f.terminal == terminal) {
          if (this.allcuisines.indexOf(f.cuisines) == -1)
            this.allcuisines.push({ text: f.cuisines });
          return f;
        }
      });
      // console.log(" this.allcuisines", this.allcuisines);
    },
    filterByCuisines() {
      console.log("filterByCuisines ", this.selectCuisines);
      this.allterminals = [];
      this.resturants = this.resturantsData.filter((f) => {
        if (f.cuisines == this.selectCuisines) {
          if (this.allterminals.indexOf(f.terminal) == -1)
            this.allterminals.push({ text: f.terminal });
          return f;
        }
      });
    },
    clearFilter() {
      this.resturants = this.resturantsData;
      this.allcuisines = this.cuisines;
      this.allterminals = this.terminalData;
      this.selectedTerminal = this.allterminals[1].text;
      this.filterByTerminal(this.allterminals[1].text);
      this.selectCuisines = "";
      this.search = "";
    },
  },

  computed: {},
};
</script>
<style scoped>
.diningdirectoryBlock .v-sheet.v-card:not(.v-sheet--outlined),
.fooddrinkImg .v-sheet.v-card:not(.v-sheet--outlined) {
  box-shadow: none;
}

.imageBox-txt.v-card__actions {
  padding: 0 !important;
}

.v-dialog.FandD-dialogBox {
  overflow: hidden;
}

.v-dialog.FandD-dialogBox h2 {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #1b63f8;
}

.v-dialog.FandD-dialogBox h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -0.02em;
  color: #374151;
}

.v-dialog.FandD-dialogBox h3 {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  color: #374151;
}

.v-dialog.FandD-dialogBox p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #6b7280;
}

.dtlrestu {
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: -0.02em;
  color: #374151;
}

.carouselText .v-card__actions {
  padding: 0;
}
.modalText h2 {
  font-size: 12px;
  margin-top: 2px;
}
.modalText h1 {
  font-size: 14px;
}
.modalText h3 {
  font-size: 10px;
}
.modalText p {
  font-size: 10px;
}

.modalText2 p {
  font-size: 12px !important;
  margin-bottom: 0;
}
.modalText2 span {
  font-size: 10px !important;
  line-height: 12px;
}
.modalText2 .v-btn {
  margin-top: 5px;
}
.modalText2 .gradient-button {
  font-size: 10px !important;
}

.modalText2 .v-btn.filterBtn {
  min-width: 72px !important;
}

.searchBox {
  max-width: 1050px;
  margin: 0 auto;
}
.searchBox .v-list-item {
  padding: 0 !important;
}

